.container {
    min-height: 100vh;
    display: flex;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    font-size: calc(24px + 4vmin);
}

img {
    margin-inline: 2em;
    align-self: center;
}

.gradient {
    font-weight: 900;
    background: -webkit-linear-gradient(160deg, #0093E9 21%, #80D0C7 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    h1 {
        align-self: center;
        font-size: 28px;
        font-weight: 300;
        opacity: 0;
        transform: translateY(-200px)
    }

    h1 strong {
        font-size: 36px;
        font-weight: 900;
        align-self: center;
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
    h1 {
        align-self: center;
        font-size: 42px;
        font-weight: 300;
        opacity: 0;
        transform: translateY(-200px)
    }

    h1 strong {
        font-size: 64px;
        font-weight: 900;
        align-self: center;
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    h1 {
        align-self: center;
        font-size: 52px;
        font-weight: 300;
        opacity: 0;
        transform: translateY(-200px)
    }

    h1 strong {
        font-size: 64px;
        font-weight: 900;
        align-self: center;
    }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
    h1 {
        font-size: 64px;
        font-weight: 300;
        align-self: center;
        opacity: 0;
        transform: translateY(-200px)
    }
    
    h1 strong {
        font-size: 64px;
        font-weight: 900;
        align-self: center;
    }
}
